<template>
  <div class="minesweeper-cell" :class="getClass()">
    <div v-if="cell.isOpen && cell.bombCount">
      {{ cell.bombCount }}
    </div>
    <div v-if="cell.hasFlag">
      ❗
    </div>
    <div v-if="cell.hasBomb && cell.isOpen">
      💣
    </div>
  </div>
</template>

<script>
export default {
  name: "cells",
  props: {
    cell: {
      type: Object,
      required: true,
    },
  },
  methods: {
    //Get the type of cell (opened / has bomb or has flag)
    getClass() {
      const { cell } = this;
      if (cell.isOpen) {
        return "minesweeper-open";
      }
      if (cell.hasFlag) {
        return "minesweeper-flag";
      }
      if (cell.isOpen && cell.hasBomb) {
        return "minesweeper-bomb";
      }
      return "";
    },
  },
};
</script>

<style lang="scss">
.minesweeper {
  &-cell {
    align-items: center;
    background: rgba(0, 0, 0, 0.1);
    border: 1px white solid;
    color: #2c3e50;
    cursor: pointer;
    display: flex;
    font-size: 1.3em;
    justify-content: center;
    min-height: 35px;
    min-width: 35px;
  }

  &-bomb {
    background: #c0392b;
  }
  &-defuse {
    background: orange;
  }

  &-open {
    background: rgba(0, 0, 0, 0.05);
  }

  &-flag {
    background: #2ecc71;
  }
}
</style>
