<template>
  <div>{{ theTime.toFixed(1) }}</div>
</template>

<script>
export default {
  name: "timer",
  props: {
    finished: {
      type: Boolean,
      required: true,
    },
  },
  data() {
    return {
      theTime: 0,
      timer: false,
      timerReq: false,
    };
  },
  mounted() {
    this.initTimer();
  },
  methods: {
    //Initialise timer
    initTimer() {
      if (this.finished) {
        window.cancelAnimationFrame(this.timerReq);
        return;
      }
      this.timer = false;
      this.timerReq = window.requestAnimationFrame(this.setTimer);
    },
    // Set the timer (real time - game start time)
    setTimer(realtime) {
      if (!this.timer) {
        this.timer = realtime;
      }
      this.theTime = (realtime - this.timer) / 1000;
      if (!this.finished) {
        window.requestAnimationFrame(this.setTimer);
      }
    },
  },
  // Watchers
  watch: {
    // Watch the status of the game (Reset button)
    finished() {
      this.initTimer();
    },
  },
};
</script>

