<template>
  <router-link :to="{ name: 'Home' }" aria-label="Back">
    <i class="fa fa-arrow-left"></i>
  </router-link>

  <div class="container pb-3">
    <div class="row justify-content-md-center">
      <BoardGame :bombs="bombs" :cols="cols" :rows="rows" />
    </div>
  </div>
</template>

<script>
import BoardGame from "../components/BoardGame.vue";
export default {
  components: { BoardGame },
  data() {
    return {
      bombs: this.bombs,
      cols: this.cols,
      rows: this.rows,
    };
  },
  beforeMount() {
    this.setDifficulty(this.$route.params);
  },
  methods: {
    // Set the difficulty of the game
    setDifficulty(params) {
      const { difficulty } = params;
      this.difficulty = difficulty;
      this.initializeParams(difficulty);
    },
    // Adjust setting based on difficulty
    initializeParams(difficulty) {
      switch (difficulty) {
        case "easy": {
          this.bombs = 10;
          this.cols = 9;
          this.rows = 9;
          break;
        }
        case "medium": {
          this.bombs = 25;
          this.cols = 12;
          this.rows = 12;
          break;
        }

        case "hard": {
          this.bombs = 50;
          this.cols = 14;
          this.rows = 14;
          break;
        }

        case "expert": {
          this.bombs = 80;
          this.cols = 14;
          this.rows = 14;
          break;
        }
      }
    },
  },
};
</script>

<style>
.container {
  width: 100;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}
.pb-3 {
  padding-bottom: 1rem !important;
}
</style>
